
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  padding-bottom: 100px
}
.test-inputer {
  margin-top: 50px
}
code {
  background: #35495e;
  padding: 5px 20px;
  display: inline-block;
  color: #ffeb59;
  font-size: 28px;
  line-height: 1.5;
  border-radius: 5px
}
h1, h2 {
  font-weight: 400
}
ul {
  list-style-type: none;
  padding: 0
}
li {
  display: inline-block;
  margin: 0 10px
}
a {
  color: #42b983
}
.img-inputer {
  margin-top: 50px;
}



